import React from 'react';
import { getElementById } from '../../extensions/ElementExtension';
import { guid8 } from '../../extensions/HashFuncs';
import { componentProps } from '../componentDefine';
import ComboBox, { comboBoxItemProps } from './ComboBox';
import thisCss from './InputSection.module.scss';
import { sleep } from '../../extensions/ThreadExtension';


function InputSection(props?: componentProps & {
    label?: string,
    description?: string,
    inputType?: "text" | "password" | "checkbox" | "radio" | "richtext",
    defaultChecked?: boolean,
    defaultValue?: string,
    onClick?: (input?: any) => void,
    onChange?: (value: string | boolean, text?: string) => void,
    minHeight?: number,
    placeHolder?: string,
    readOnly?: boolean
}) {
    const thisId = props.id ?? guid8();

    let containerClassName = thisCss["InputSection-container"];
    if (props?.className) {
        containerClassName += ` ${props.className}`;
    }

    async function updateCheck() {
        const inputObj = getElementById(thisId) as HTMLInputElement;
        if (inputObj) {
            inputObj.checked = !inputObj.checked;
            if (inputObj.checked) {
                if (props.onClick) {
                    await sleep(1);
                    props.onClick(inputObj);
                }
            }
            if (props.onChange) {
                props.onChange(inputObj.checked, inputObj.checked.toString());
            }
        }

    }

    try {
        let labelFirst = true;
        let labelPointer = false;
        let inputElement: JSX.Element = undefined;
        if (props.inputType === "text") {
            inputElement = (<div className={thisCss["InputSection-text-container"]}>
                <input className={thisCss["InputSection-text"]} id={thisId} type="text" defaultValue={props.defaultValue} readOnly={props.readOnly}
                    onChange={evt => {
                        if (props.onChange) {
                            props.onChange(evt.currentTarget.value, evt.currentTarget.value);
                        }
                    }}
                    placeholder={props.placeHolder}
                />
            </div>);
        }
        else if (props.inputType === "password") {
            inputElement = (<div className={thisCss["InputSection-text-container"]}>
                <input className={thisCss["InputSection-password"]} id={thisId} type="password" defaultValue={props.defaultValue} readOnly={props.readOnly}
                    onChange={evt => {
                        if (props.onChange) {
                            props.onChange(evt.currentTarget.value, evt.currentTarget.value);
                        }
                    }}
                />
            </div>);
        }
        else if (props.inputType === "checkbox") {
            inputElement = (<div className={thisCss["InputSection-checkbox"]}>
                <input id={thisId} type="checkbox" defaultChecked={props.defaultChecked} readOnly={props.readOnly}
                // onChange={evt => {
                //     if (props.onChange) {
                //         props.onChange(evt.currentTarget.checked, evt.currentTarget.value);
                //     }
                // }}
                />
            </div>);
            labelFirst = false;
            labelPointer = true;
        }
        else if (props.inputType === "radio") {
            inputElement = (<div className={thisCss["InputSection-radio"]} >
                <input id={thisId} type="radio" defaultChecked={props.defaultChecked} readOnly={props.readOnly}
                // onChange={evt => {
                //     if (props.onChange) {
                //         props.onChange(evt.currentTarget.checked, evt.currentTarget.value);
                //     }
                // }}
                />
            </div>);
            labelFirst = false;
            labelPointer = true;
        }
        else if (props.inputType === "richtext") {
            inputElement = (<div className={thisCss["InputSection-texteara"]}>
                <textarea id={thisId}
                    style={{ minHeight: props.minHeight }}
                    onChange={evt => {
                        if (props.onChange) {
                            props.onChange(evt.currentTarget.textContent, evt.currentTarget.value);
                        }
                    }}
                    placeholder={props.placeHolder}
                    readOnly={props.readOnly}
                    value={props.defaultValue}
                />
            </div>);
        }

        let labelElement: JSX.Element = undefined;
        if (props.label) {
            let labelClassName = thisCss["InputSection-label"];
            if (labelPointer) {
                labelClassName += " " + thisCss["InputSection-label-pointer"];
            }
            labelElement = (<div className={labelClassName} >{props.label}</div>);
            if (labelFirst) {
                labelElement = (<div className={labelClassName}>{props.label}:</div>);
            }

        }

        let descriptionElement: JSX.Element = undefined;
        if (props.description) {
            descriptionElement = (<div className={thisCss["InputSection-description"]}>{props.description}</div>);
        }

        let contents = labelFirst
            ? <div id={`${thisId}-container`} className={containerClassName}>
                {labelElement}
                {inputElement}
                {descriptionElement}
            </div>
            : <div id={`${thisId}-container`} className={containerClassName} onClick={updateCheck}>
                {inputElement}
                {labelElement}
                {descriptionElement}
            </div>;
        return contents;
    }
    catch (e) {
        console.log(e);
    }
}
export default InputSection;