import { useEffect, useRef, useState, lazy, MouseEvent, startTransition } from 'react';
import { componentWithApiProps } from '../components/componentDefine';
import { closeDialog, closeProcessing, ConfirmButtons, DialogOptions, openDialog, openProcessing } from '../components/forms/dialog/Dialog';
import ButtonFilter from '../components/forms/filters/ButtonFilter';
import Filters from '../components/forms/filters/Filters';
import { itemFilterProps } from '../components/forms/filters/filtersDefine';
import LabelFilter from '../components/forms/filters/LabelFilter';
import OptionsFilter from '../components/forms/filters/OptionsFilter';
import Image from '../components/forms/Image';
import Layout from '../components/forms/Layout';
import { dataResponse, rowProps } from '../components/forms/table/PagingTable';
import PagingTableV2 from '../components/forms/table/PagingTableV2';
import ToolStripItem from '../components/forms/toolstrip/ToolStripItem';
import ToolStripMenu from '../components/forms/toolstrip/ToolStripMenu';
import { navigate } from '../components/router/methods';
import { guid8 } from '../extensions/HashFuncs';
import { getAsync, postAsync } from '../extensions/HttpExtension';
import thisCss from './List.module.scss';
import ChangeParent from './ChangeParent';
import InputSection from '../components/forms/InputSection';
import { getElementById } from '../extensions/ElementExtension';
import { sleep } from '../extensions/ThreadExtension';
const WorkLog = lazy(() => import('./WorkLog'));
const EstimateWorkLogs = lazy(() => import('./EstimateWorkLogs'));
function List(props?: componentWithApiProps & {
    headers?: any
}) {

    const now = new Date();
    const timezoneOffset = now.getTimezoneOffset();

    const thisId = props.id ?? guid8();
    const processingId = `${thisId}-processing`;
    const [boards, setBoards] = useState<itemFilterProps[]>(undefined);
    const boardRef = useRef<any>();
    const [sprints, setSprints] = useState<itemFilterProps[]>(undefined);
    const sprintRef = useRef<any>();
    const [projects, setProjects] = useState<itemFilterProps[]>(undefined);
    const projectRef = useRef<any>();
    const [tableKey, setTableKey] = useState<string>(undefined);
    const [usageTableKey, setUsageTableKey] = useState<string>(undefined);
    const [mode, setMode] = useState<number>(0);
    const [users, setUsers] = useState<any[]>(undefined);

    const jUrl = localStorage.getItem('j-url');
    const jName = sessionStorage.getItem('j-name');
    const jAccountId = sessionStorage.getItem('j-accountid');
    const zIndex = 1;
    useEffect(() => {

        if (!jName) {
            navigate('/jira/login');
        }
        startTransition(reload);
        // reload();
    }, []);

    function reload() {
        try {
            loadBoards();
            loadUsers();
            selectedStoryTaskKeysRef.current = [];
            selectedUsageTaskKeysRef.current = [];
        }
        catch (e) {
        }
    }
    function loadBoards() {
        try {
            setBoards(undefined);
            setSprints(undefined);
            setProjects(undefined);
            const url = `${props.apiUrl}/api/jira/getallboards`;
            getAsync(url, props.headers, async response => {
                if (response.status == 200) {
                    const rObjs = await response.json();
                    setBoards(rObjs.map((b: any) => { return { id: b.ID, name: b.Name, checked: b.ID == boardRef?.current?.ID } }));
                }
            });
        }
        catch (e) {
            console.log(e);
        }
    }
    function loadUsers() {
        try {
            const url = `${props.apiUrl}/api/jira/users`;
            getAsync(url, props.headers, async response => {
                if (response.status == 200) {
                    const rObjs = await response.json();
                    setUsers(rObjs);
                }
            });
        }
        catch (e) {
            console.log(e);
        }
    }

    function applyBoardClick(itemObjs?: any[]) {
        try {
            boards?.forEach(ite => {
                ite.checked = false;
            });
            if (itemObjs?.length > 0) {
                const id = itemObjs[0].id;
                boards.forEach(ite => {
                    if (ite.id.toString() === id) {
                        ite.checked = true;
                        boardRef.current = ite;
                    }
                });
                loadSprints(id);
                loadProjects(id);
            }
        }
        catch (e) {
        }
    }
    function loadSprints(boardId: string) {
        try {
            const url = `${props.apiUrl}/api/jira/getsprints?boardId=${boardId}&timezoneOffset=${timezoneOffset}`;
            openProcessing(processingId);
            getAsync(url, props.headers, async response => {
                if (response.status == 200) {
                    const rObjs = await response.json();
                    setSprints(rObjs.map((b: any, idx: number) => { return { id: b.ID, name: `${b.Name} [${b.StartDateValue} - ${b.EndDateValue}]`, checked: sprintRef?.current?.ID == b.ID || idx == 0 } }));

                }
            }, undefined, () => closeProcessing(processingId));
        }
        catch (e) {
            console.log(e);
        }
    }
    function applySprintClick(itemObjs?: any[]) {
        try {
            sprints?.forEach(ite => {
                ite.checked = false;
            });
            if (itemObjs?.length > 0) {
                const id = itemObjs[0].id;
                sprints?.forEach(ite => {
                    if (ite.id.toString() === id) {
                        ite.checked = true;
                    }
                });
            }
        }
        catch (e) {
        }
    }

    function loadProjects(boardId: string) {
        try {
            const url = `${props.apiUrl}/api/jira/getprojects?boardId=${boardId}`;
            openProcessing(processingId);
            getAsync(url, props.headers, async response => {
                if (response.status == 200) {
                    const rObjs = await response.json();
                    setProjects(rObjs.map((b: any, idx: number) => { return { id: b.ID, name: b.Name, checked: idx == 0 || projectRef?.current?.ID == b.ID } }));

                }
            }, undefined, () => closeProcessing(processingId));
        }
        catch (e) {
            console.log(e);
        }
    }
    function applyProjectClick(itemObjs?: any[]) {
        try {
            projects.forEach(ite => {
                ite.checked = false;
            });
            if (itemObjs?.length > 0) {
                const id = itemObjs[0].id;
                projects.forEach(ite => {
                    if (ite.id.toString() === id) {
                        ite.checked = true;
                    }
                });
            }
        }
        catch (e) {
        }
    }
    function onSearchClick(evt?: React.MouseEvent) {
        setTableKey(guid8());
    }
    function fetchData(pageSize?: number, pageNumber?: number) {
        try {
            if (!tableKey) {
                return;
            }
            selectedStoryTaskKeysRef.current = [];
            openProcessing(processingId);

            pageSize = pageSize;
            const sprintObj = sprints.find(ite => ite.checked);
            const projectObj = projects.find(ite => ite.checked);
            if (sprintObj || projectObj) {

                sprintRef.current = sprintObj;
                projectRef.current = projectObj;

                let url = `${props.apiUrl}/api/jira/getstoryinsprint?includeSubtasks`;
                if (sprintObj) {
                    url += `&sprintId=${sprintObj.id}`;
                }
                if (projectObj) {
                    url += `&pjKey=${projectObj.id}`;
                }

                const taskRowSelector = (task: any, itemIdx: number, rowIdx: number, parentIdx: number, hasWorklogs?: boolean) => {
                    let status: string = task.Status;
                    status = status.replace(' ', '').toLowerCase();

                    var assigneeObj = undefined;
                    if (users?.length > 0) {
                        assigneeObj = users.find(ite => ite.AccountId == task.AssigneeId);
                    }
                    const taskIdx = parentIdx * (itemIdx + rowIdx);
                    const rowObj: rowProps =
                    {
                        id: task.Key,
                        className: parentIdx > 0 ? thisCss["List-list-row-subtask"] : thisCss["List-list-row-story"],
                        cells: [
                            { text: parentIdx > 0 ? `${parentIdx}.${itemIdx + rowIdx}` : `${itemIdx + rowIdx}`, align: 'center' },
                            { text: `${task.Key}`, align: 'center', element: (<a className={thisCss["List-list-task-link"]} target="_blank" href={`${jUrl}/browse/${task.Key}`}>{task.Key}</a>) },
                            {
                                element: (<div contentEditable className={parentIdx > 0 ? thisCss["List-list-txt-summary"] : thisCss["List-list-txt-psummary"]} defaultValue={task.Summary} >{task.Summary}</div>)
                            },
                            {
                                text: `${task.Assignee}`,
                                element: assigneeObj ? (<div className={thisCss["List-list-assignee"]}><Image className={thisCss["List-list-assignee-image"]} src={assigneeObj.AvatarUrl} /> {assigneeObj.DisplayName}</div>) : (<div className={thisCss["List-list-assignee"]}>{task.Assignee}</div>)
                            },
                            {
                                align: 'center',
                                element: (<div className={thisCss[`List-list-task-status-${status}`]}>{task.Status}</div>)
                            },
                            { align: 'center', element: (<div><input className={thisCss["List-list-txt-sps"]} type="number" defaultValue={task.StoryPoints} /></div>) },
                            { align: "center", element: hasWorklogs ? (<WorkLog id={`${thisId}-${task.Key}-worklogs`} delayMiliseconds={taskIdx * 500} storyPoints={task.StoryPoints} headers={props.headers} apiUrl={props.apiUrl} issueKey={task.Key} />) : (<span></span>) }
                        ]
                    };
                    return rowObj;
                };

                const convertData = async (response: any) => {
                    if (response.status === 200) {
                        const data = await response.json();
                        const rIdx = pageSize * (pageNumber - 1) + 1;
                        const records: rowProps[] = [];
                        [...data].forEach((ite, idx) => {
                            records.push(taskRowSelector(ite.MainTask, idx, rIdx, 0, !ite.HasSubTasks));
                            if (ite.SubTasks?.length > 0) {
                                [...ite.SubTasks].forEach((sIte, sIdx) => {
                                    records.push(taskRowSelector(sIte, sIdx, 1, idx + 1, true));
                                });
                            }
                        });
                        const result: dataResponse = {
                            records: records
                        };

                        return result;
                    }
                    else if (response.status === 401) {
                        navigate("/jira/login");
                    }
                    else {
                        return {
                            totalPages: 0,
                            totalRecords: 0,
                            pageNumber: 0
                        }
                    }
                };
                return getAsync(url, props.headers,
                    convertData,
                    async err => {
                        if (err.status === 401) {
                            navigate('/jira/login');
                        }
                        return {
                            totalPages: 0,
                            totalRecords: 0,
                            pageNumber: 0
                        }
                    }, () => {
                        closeProcessing(processingId);
                        setUsageTableKey(guid8());

                    }
                );
            }


        }
        catch (e) {
        }
        return Promise.resolve({
            totalPages: 0,
            totalRecords: 0,
            pageNumber: 0,
            records: []
        });
    }
    function fetchUsageData(pageSize?: number, pageNumber?: number) {
        try {
            if (!tableKey) {
                return;
            }
            selectedUsageTaskKeysRef.current = [];
            openProcessing(processingId);
            pageSize = pageSize;
            const url = `${props.apiUrl}/api/jira/getusageinsprint`;
            const convertData = async (response: any) => {
                if (response.status === 200) {
                    const data = await response.json();
                    const rIdx = pageSize * (pageNumber - 1) + 1;
                    const records: rowProps[] = [];
                    [...data].forEach((ite, idx) => {
                        const assigneeObj = users.find(u => u.AccountId == ite.AssigneeId);
                        const assigneeRowObj: rowProps =
                        {
                            className: thisCss["List-usage-list-row-assignee"],
                            cells: [
                                // { text: `${idx + 1}`, align: 'center' },
                                {
                                    text: `${ite.Assignee}`, colSpan: 3,
                                    element: assigneeObj ? (<div className={thisCss["List-list-assignee"]}><Image className={thisCss["List-list-assignee-image"]} src={assigneeObj.AvatarUrl} /> {assigneeObj.DisplayName}</div>) : (<div className={thisCss["List-list-assignee"]}>Unassign</div>)
                                },
                                { text: `${ite.Total}`, align: 'center' },
                                { element: (ite.AssigneeId == jAccountId ? (<div className={thisCss["List-usage-list-row-estimate-worklogs"]} onClick={() => onEstimateWorklogs(ite.AssigneeId)}>Estimate</div>) : ""), align: "center" },
                                // { element: (<div className={thisCss["List-usage-list-row-estimate-worklogs"]} onClick={() => onEstimateWorklogs(ite.AssigneeId)}>Estimate</div>), align: "center" },
                            ]
                        };
                        records.push(assigneeRowObj);
                        if (ite.Tasks?.length > 0) {
                            [...ite.Tasks].forEach(t => {
                                if (t.Key) {
                                    if (t.Tasks?.length > 0) {
                                        const storyRowObj: rowProps = {
                                            className: thisCss["List-usage-list-row-ptask"],
                                            cells: [
                                                { align: 'center', element: (<div className={thisCss["List-list-task-container"]}><input onClick={evt => onTaskCheckClick(evt, t.Key)} type="checkbox" value={t.Key} /><a className={thisCss["List-list-task-link"]} target="_blank" href={`${jUrl}/browse/${t.Key}`}>{t.Key}</a></div>) },
                                                { element: (<div contentEditable={!(t.Tasks?.length > 0)} className={thisCss["List-list-txt-psummary"]}>{t.Summary}</div>), text: `${t.Summary}`, colSpan: 2 },
                                                {
                                                    align: 'center',
                                                    element: t.Tasks?.length > 0 ? <div></div> : (<div><input className={thisCss["List-list-txt-sps"]} type="number" defaultValue={t.StoryPoints} /></div>)
                                                },
                                                {
                                                    align: "center",
                                                    element: t.Tasks?.length > 0 ? (<div></div>) : (<WorkLog id={guid8()} delayMiliseconds={500} storyPoints={t.StoryPoints} headers={props.headers} apiUrl={props.apiUrl} issueKey={t.Key} />)
                                                }
                                            ]
                                        };
                                        records.push(storyRowObj);
                                        [...t.Tasks].forEach((st: any, stIdx) => {
                                            let status: string = st.Status;
                                            status = status.replace(' ', '').toLowerCase();
                                            const subTaskRowObj: rowProps = {
                                                className: thisCss["List-usage-list-row-task"],
                                                cells: [
                                                    { align: 'center', element: (<div className={thisCss["List-list-task-container"]}><input onClick={evt => onTaskCheckClick(evt, st.Key)} type="checkbox" value={st.Key} /><a className={thisCss["List-list-task-link"]} target="_blank" href={`${jUrl}/browse/${st.Key}`}>{st.Key}</a></div>) },
                                                    { element: (<div contentEditable className={thisCss["List-list-txt-summary"]}>{st.Summary}</div>) },
                                                    {
                                                        align: "center",
                                                        element: (<div className={thisCss[`List-list-task-status-${status}`]}>{st.Status}</div>)
                                                    },
                                                    { element: (<div><input className={thisCss["List-list-txt-sps"]} type="number" defaultValue={st.StoryPoints} /></div>), align: 'center' },
                                                    { align: "center", element: (<WorkLog id={guid8()} delayMiliseconds={500} storyPoints={st.StoryPoints} headers={props.headers} apiUrl={props.apiUrl} issueKey={st.Key} />) }
                                                ]
                                            };
                                            records.push(subTaskRowObj);
                                        });
                                    }
                                    else {
                                        let status: string = t.Status;
                                        if (status) {
                                            status = status.replace(' ', '').toLowerCase();
                                        }
                                        const storyRowObj: rowProps = {
                                            className: thisCss["List-usage-list-row-ptask"],
                                            cells: [
                                                // { text: ``, align: 'center' },
                                                { align: 'center', element: (<div className={thisCss["List-list-task-container"]}><input onClick={evt => onTaskCheckClick(evt, t.Key)} type="checkbox" value={t.Key} /><a className={thisCss["List-list-task-link"]} target="_blank" href={`${jUrl}/browse/${t.Key}`}>{t.Key}</a></div>) },
                                                { element: (<div contentEditable={!(t.Tasks?.length > 0)} className={thisCss["List-list-txt-psummary"]}>{t.Summary}</div>), text: `${t.Summary}` },
                                                {
                                                    align: "center",
                                                    element: (<div className={thisCss[`List-list-task-status-${status}`]}>{t.Status}</div>)
                                                },
                                                {
                                                    align: 'center',
                                                    element: t.Tasks?.length > 0 ? <div></div> : (<div><input className={thisCss["List-list-txt-sps"]} type="number" defaultValue={t.StoryPoints} /></div>)
                                                },
                                                {
                                                    align: "center",
                                                    element: t.Tasks?.length > 0 ? (<div></div>) : (<WorkLog id={guid8()} delayMiliseconds={500} storyPoints={t.StoryPoints} headers={props.headers} apiUrl={props.apiUrl} issueKey={t.Key} />)
                                                }
                                            ]
                                        };
                                        records.push(storyRowObj);
                                    }
                                }
                            });
                        }

                        // records.push(taskRowSelector(ite, 0, 0, 0));
                    });
                    const result: dataResponse = {
                        records: records
                    };
                    return result;
                }
                else if (response.status === 401) {
                    navigate("/jira/login");
                }
                else {
                    return {
                        totalPages: 0,
                        totalRecords: 0,
                        pageNumber: 0,
                        records: []
                    }
                }
            };
            return getAsync(url, props.headers,
                convertData,
                async err => {
                    if (err.status === 401) {
                        navigate('/jira/login');
                    }
                    return {
                        totalPages: 0,
                        totalRecords: 0,
                        pageNumber: 0,
                        records: []
                    }
                }, () => closeProcessing(processingId));
        }
        catch (e) {
        }
        return Promise.resolve({
            totalPages: 0,
            totalRecords: 0,
            pageNumber: 0,
            records: []
        });
    }

    function removeClonePrefix() {
        try {
            var summaryClassNames = [thisCss["List-list-txt-summary"], thisCss["List-list-txt-psummary"]];
            summaryClassNames.forEach(summaryClassName => {
                try {
                    var summaryObjs = document.getElementsByClassName(summaryClassName) as HTMLCollectionOf<HTMLDivElement>;
                    if (summaryObjs?.length > 0) {

                        [...summaryObjs].forEach((summaryObj, rIdx) => {
                            try {

                                let summary = summaryObj.innerText;
                                if (summary.indexOf("CLONE - ") == 0) {
                                    summary = summary.substring(8);
                                    summaryObj.innerText = summary;
                                }
                            }
                            catch (e) {
                            }
                        });
                    }
                }
                catch (e) {
                }
            });
        }
        catch (e) {
            console.log(e);
        }
    }

    function onUpdateStory() {
        const dUpdateStory: DialogOptions = {
            zIndex: 1,
            id: guid8(),
            body: (<div className={thisCss["List-dialog-updatestory"]}>
                Are you sure?
            </div>),
            title: 'Update stories',
            confirmButtons: ConfirmButtons.YesNo,
            onAcceptHandler: updateStory
        };
        openDialog(dUpdateStory);
    }
    function updateStory() {
        try {
            var taskObjs: any[] = [];
            var rowClassNames = [thisCss["List-list-row-story"], thisCss["List-list-row-subtask"]];
            rowClassNames.forEach(rowClassName => {
                try {
                    var taskRowObjs = document.getElementsByClassName(rowClassName) as HTMLCollectionOf<HTMLTableRowElement>;
                    if (taskRowObjs?.length > 0) {

                        [...taskRowObjs].forEach((rowObj, rIdx) => {
                            try {
                                const key = (rowObj.childNodes[2] as HTMLTableColElement).innerText;
                                const summary = (rowObj.childNodes[3] as HTMLTableColElement).innerText;
                                let storypoints = 0;
                                try {
                                    storypoints = parseInt((rowObj.childNodes[6] as HTMLTableColElement).getElementsByTagName("input")[0].value);
                                }
                                catch (e) {
                                }
                                if (storypoints > 0) {
                                    taskObjs.push({ key, summary, storypoints });
                                }
                            }
                            catch (e) {
                            }
                        });
                    }
                }
                catch (e) {
                }
            });
            if (taskObjs?.length > 0) {
                let url = `${props.apiUrl}/api/jira/issue/update`;
                postAsync(url, taskObjs, props.headers, async response => {
                    if (response.status === 200) {

                    }
                }, undefined, () => setTableKey(guid8()));
            }
        }
        catch (e) {
            console.log(e);
        }
    }
    function onUpdateStorage() {
        const dUpdateStory: DialogOptions = {
            zIndex: 1,
            id: guid8(),
            body: (<div className={thisCss["List-dialog-updatestorage"]}>
                Are you sure?
            </div>),
            title: 'Update storages',
            confirmButtons: ConfirmButtons.YesNo,
            onAcceptHandler: updateStorage
        };
        openDialog(dUpdateStory);
    }
    function updateStorage() {
        try {
            var taskObjs: any[] = [];
            var rowClassNames = [thisCss["List-usage-list-row-ptask"], thisCss["List-usage-list-row-task"]];
            rowClassNames.forEach(rowClassName => {
                try {
                    var taskRowObjs = document.getElementsByClassName(rowClassName) as HTMLCollectionOf<HTMLTableRowElement>;
                    if (taskRowObjs?.length > 0) {

                        [...taskRowObjs].forEach((rowObj, rIdx) => {
                            try {
                                const key = (rowObj.childNodes[0] as HTMLTableColElement).innerText;
                                const summary = (rowObj.childNodes[1] as HTMLTableColElement).innerText;
                                let storypoints = 0;
                                try {
                                    storypoints = parseInt((rowObj.childNodes[3] as HTMLTableColElement).getElementsByTagName("input")[0].value);
                                }
                                catch (e) {
                                }
                                if (storypoints > 0) {
                                    taskObjs.push({ key, summary, storypoints });
                                }
                            }
                            catch (e) {
                            }
                        });
                    }
                }
                catch (e) {
                }
            });
            if (taskObjs?.length > 0) {
                let url = `${props.apiUrl}/api/jira/issue/update`;
                postAsync(url, taskObjs, props.headers, async response => {
                    if (response.status === 200) {

                    }
                }, undefined, () => { setTableKey(guid8()); });
            }
        }
        catch (e) {
            console.log(e);
        }
    }

    function onEstimateWorklogs(accountId?: string) {
        const dUpdateStory: DialogOptions = {
            zIndex: 1,
            id: guid8(),
            body: (<div className={thisCss["List-dialog-estimateworklogs"]}>
                <EstimateWorkLogs apiUrl={props.apiUrl} headers={props.headers} sprint={sprintRef.current} project={projectRef.current} accountId={accountId} onFinish={() => {
                    setTableKey(guid8());
                }} />
            </div>),
            title: 'Estimate work logs',
            confirmButtons: ConfirmButtons.Close,

        };
        openDialog(dUpdateStory);
    }

    const selectedUsageTaskKeysRef = useRef<string[]>([]);
    function onTaskCheckClick(evt?: MouseEvent<HTMLInputElement>, key?: string) {
        // evt.preventDefault();
        try {
            if (evt.currentTarget.checked) {
                if (!selectedUsageTaskKeysRef.current.includes(key)) {
                    selectedUsageTaskKeysRef.current.push(key);
                }
            }
            else {
                if (selectedUsageTaskKeysRef.current.includes(key)) {
                    selectedUsageTaskKeysRef.current = selectedUsageTaskKeysRef.current.filter(ite => ite !== key);
                }
            }
        }
        catch (e) {
            console.log(e);
        }
    }

    const selectedStoryTaskKeysRef = useRef<string[]>([]);
    const parentKeyRef = useRef<string>('');
    function onChangeParent(keys?: string[]) {
        if (keys?.length > 0) {
            const dialogId = `${thisId}-change-parent-dialog`;
            const dUpdateStory: DialogOptions = {
                zIndex: zIndex + 1,
                id: dialogId,
                body: (<div className={thisCss["List-dialog-change-parent"]}>
                    <div className={thisCss["List-dialog-change-parent-confirmation"]}>Do you want to change parent for task(s)<br /><br />{keys.map((k, idx) => (<span className={thisCss["List-dialog-change-parent-skey"]}><wbr /><b>{k}</b>{(idx < keys.length - 1 ? (<>, </>) : undefined)}</span>))}?</div>
                    <InputSection onChange={(v, t) => parentKeyRef.current = t} id={`${thisId}-parent-key`} label='Parent Key' inputType="text" />
                </div >),
                title: 'Change Parent',
                confirmButtons: ConfirmButtons.OkCancel,
                okButtonTitle: 'Update',

                onPreAcceptHandler: () => {
                    const txt = getElementById(`${thisId}-parent-key`) as HTMLInputElement;
                    if (!txt?.value) {
                        openDialog({
                            title: 'Change Parent',
                            body: 'Please type parent key',
                            zIndex: zIndex + 2
                        })
                        return false;
                    }

                    else {
                        const confirmDialogId = `${thisId}-change-parent-confirm-dialog`;
                        const dConfirm: DialogOptions =
                        {
                            id: confirmDialogId,
                            zIndex: zIndex + 2,
                            title: 'Change Parent',
                            body: 'Do you want to do this?',
                            confirmButtons: ConfirmButtons.YesNo,
                            onAcceptHandler: () => updateParent(txt.value, keys, dialogId),
                            onDenyHandler: () => closeDialog(dialogId)
                        }
                        openDialog(dConfirm);
                    }
                }
            };
            openDialog(dUpdateStory);
        }
        else {
            openDialog({
                title: 'Change Parent',
                body: 'Please select at least 01 task',
                zIndex: zIndex + 1
            })
        }
    }
    function updateParent(parentKey: string, keys: string[], dialogId: string) {
        if (parentKey && keys?.length > 0) {
            const url = `${props.apiUrl}/api/jira/issue/changeparent`;
            openProcessing(processingId);
            postAsync(url, { parentKey, keys }, props.headers, async response => {
                if (response.status === 200) {
                    const resultObjs = await response.json();
                    if (resultObjs?.length > 0) {
                        const failedObjs = resultObjs.filter((ite: any) => !ite.Status);
                        if (failedObjs?.length > 0) {
                            await sleep(1000);
                            openDialog({
                                title: 'Change Parent',
                                body: (<div className={thisCss["List-dialog-change-parent"]}>{'Failed to change parent for task(s) ' + failedObjs.map((ite: any) => ite.Key).join(', ')}</div>),
                                zIndex: zIndex + 1
                            })
                        }
                    }
                    setTableKey(guid8());
                }
            }, undefined, () => closeProcessing(processingId));

        }
        closeDialog(dialogId);
    }

    let className = thisCss["List"];
    if (props?.className) {
        className += ` ${props.className}`;
    }
    try {
        const filterElement = (<Filters className={thisCss["List-layout-header"]}>
            <LabelFilter><b title='Click to refresh'>Filters:</b></LabelFilter>
            <ButtonFilter onClick={reload}>&#8635;</ButtonFilter>
            <OptionsFilter
                id={`${thisId}-ft-board`}
                text={`Board`}
                options={boards}
                applyClick={(evt, itemObjs) => applyBoardClick(itemObjs)}
            />
            <OptionsFilter
                id={`${thisId}-ft-sprint`}
                text={`Sprint`}
                options={sprints}
                applyClick={(evt, itemObjs) => applySprintClick(itemObjs)}
            />
            <OptionsFilter
                id={`${thisId}-ft-project`}
                text={`Project`}
                options={projects}
                applyClick={(evt, itemObjs) => applyProjectClick(itemObjs)}
            />

            <ButtonFilter
                text={`Search`}
                onClick={onSearchClick}
            />
        </Filters>);

        const jAccountId = sessionStorage.getItem('j-accountid');

        let footerElement = (<div className={thisCss["List-layout-footer"]}>
            <div>Account:</div>
            <div className={thisCss["List-layout-footer-account"]}>{jName}</div>
            <div><button onClick={() => navigate('/jira/login')}>Logout</button></div>
        </div>);

        let tableHeaders = ["1. No.", "2. Key", "3. Summary", "4. Assinee", "5. Status", "6. Story Points", "7. Work Logs"];

        let contents = (<div id={thisId} className={className}>
            <Layout
                header={filterElement}
                footer={footerElement}
                className={thisCss["List-layout"]} maxHeight={900}
            >
                <div className={thisCss["List-container-stories"] + (mode == 0 ? "" : " " + thisCss["List-container-stories-hide"])} id={`${thisId}-story-container`}>
                    <ToolStripMenu>
                        <ToolStripItem text="Menu" />
                        <ToolStripItem text="Switch to usage mode" onClick={() => setMode(1)} />
                        <ToolStripItem text=" | " />
                        <ToolStripItem text="Remove CLONE - prefix" onClick={removeClonePrefix} />
                        <ToolStripItem text="Update" onClick={onUpdateStory} />
                        <ToolStripItem text="Change Parent" onClick={evt => onChangeParent(selectedStoryTaskKeysRef.current)} />

                    </ToolStripMenu>
                    <PagingTableV2
                        key={tableKey}
                        id={`${thisId}-table-contents`}
                        headers={tableHeaders}
                        fetchData={req => fetchData(req.pageSize, req.pageNumber)}
                        noPaging
                        className={thisCss["List-list"]}
                        onInitMessage={(<div>Please select <b>Filters</b> then click <b>Search</b></div>)}
                        includeCheckbox
                        height={'90%'}
                        rowCheckChange={items => selectedStoryTaskKeysRef.current = items}
                    />
                </div>

                <div className={thisCss["List-container-usages"] + (mode == 1 ? "" : " " + thisCss["List-container-usages-hide"])} id={`${thisId}-usage-container`}>
                    <ToolStripMenu>
                        <ToolStripItem text="Menu" />
                        <ToolStripItem text="Switch to story mode" onClick={() => setMode(0)} />
                        <ToolStripItem text="Update" onClick={onUpdateStorage} />
                        <ToolStripItem text="Change Parent" onClick={evt => onChangeParent(selectedUsageTaskKeysRef.current)} />

                    </ToolStripMenu>
                    <PagingTableV2
                        key={usageTableKey}
                        id={`${thisId}-table-usage`}
                        headers={["1. Key", "2. Summary", "3. Status", "4. Story Points", "5. Work Logs"]}
                        fetchData={req => fetchUsageData(req.pageSize, req.pageNumber)}
                        noPaging
                        className={thisCss["List-usage-list"]}
                        onInitMessage={(<div>Please select <b>Filters</b> then click <b>Search</b></div>)}
                        height={'90%'}
                    />
                </div>
            </Layout>

        </div>);
        return contents;
    }
    catch (e) {
        console.log(e);
    }
}
export default List;