  position: absolute;
  width: 100%;
  overflow-y: auto;
  top: 0;
  left: 0;
}
.EBTX div[class=pTable] {
  overflow: auto;
}
.EBTX div[class=pTable]::-webkit-scrollbar {
  width: 5px;
  background-color: transparent;
}
.TaX9 {
  font-weight: bold;
  position: sticky;
  top: 0;
  z-index: 1;
}
.TaX9 tr {
  height: 30px;
}
.TaX9 tr th {
  background-color: rgb(241, 240, 240);
  border-left: solid 1px #eee;
  border-right: solid 1px #eee;
}
.TaX9 tr th input[type=checkbox] {
  width: 15px;
  height: 15px;
}
.sddy tr {
  height: 25px;
}
.sddy tr:nth-child(2n) {
  background-color: rgba(65, 105, 225, 0.0666666667);
}
.sddy tr td {
  border-left: solid 1px #eee;
  border-right: solid 1px #eee;
  padding: 4px 6px;
}
.sddy tr:hover {
  background-color: #eee;
}
.sddy tr:nth-last-child(1) td {
  border-bottom: solid 1px #eee;
}
.VjdC, .fHVv {
  width: 30px;
}
.wd4M, .F85y {
  cursor: pointer;
  width: 15px;
  height: 15px;
}
.RmJ_ {
  width: 100%;
  position: absolute;
  bottom: 0;
  min-height: 30px;
  display: flex;
  align-items: center;
  background-color: #eee;
}
.RmJ_ button {
  padding: 6px 10px;
  border: 1px solid #eee;
  border-radius: 5px 5px;
  cursor: pointer;
  min-width: 30px;
  min-height: 20px;
}
.RmJ_ button:hover {
  background-color: #0a51a5;
  color: #fff;
}
.xNo2 {
  background-color: #0a51a5;
  color: #fff;
  pointer-events: none;
}
.RmJ_ select {
  border: 1px solid #eee;
  padding: 2px 6px;
  cursor: pointer;
  border-radius: 2px 2px;
}
.RmJ_ select option {
  border: none;
}
.N883 {
  position: relative;
  right: 0;
  margin-right: 10px;
}

@media (min-width: 720px) {
  .N883 {
    position: absolute;
    right: 0;
    margin-right: 10px;
  }
