import { useEffect } from 'react';
import { componentWithApiProps } from '../../components/componentDefine';
import { guid8 } from '../../extensions/HashFuncs';
import thisCss from './AddBug.module.scss';
function AddBug(props?: componentWithApiProps & {}) {
    const thisId = props.id ?? guid8();
    let className = thisCss["AddBug"];
    if (props?.className) {
        className += ` ${props.className}`;
    }

    useEffect(() => {
        firstLoad();
    }, []);
    async function firstLoad() {
    }

    try {



        let contents = (<div id={thisId} className={className}>
            <video width={'500px'} height={500} id="video--"></video>
        </div>);
        return contents;
    }
    catch (e) {
        console.log(e);
    }
}
export default AddBug;